import React, { Component } from 'react';
import { modal, text, closeButton, visualyHidden } from './SuccessModalWindow.module.css';
import PropTypes from 'prop-types';


export default class SuccessModalWindow extends Component  {
 static propTypes = {
  closeWindow: PropTypes.func.isRequired
 }
 
  state = {
    closeWindow: false
  }

 handleCloseWindow = () => {
this.setState({
  closeWindow: true
});
 }

  render() {

    const { login } = this.props;
    const { closeWindow } = this.state;
    return (
      <div className={closeWindow ? visualyHidden : modal }> 
      <p className={text}>
        You was registred as {login.toUpperCase()}.
      </p>
      <button type='button' className={closeButton} onClick={this.handleCloseWindow}>close</button>
       </div>
    )
  }

};


