import React, { Component, Fragment } from 'react';
import shortid from 'shortid';
import { validateAll } from 'indicative/validator';
import styles from './SignUpForm.module.css';
import PropTypes from 'prop-types';



const rules = {
  login: 'required|string',
  email: 'required|email',
  password: 'required|string|min:6',
};

const messages = {
  'login.required': 'Please choose a unique user name',
  'email.email': 'Email is invalid',
  'email.required': 'Enter valid email address',
  'password.required': 'Enter a valid password',
  'password.min': 'The value is too small, mast be more then 6 characters',
};
const Gender = {
  MALE: 'male',
  FEMALE: 'female',
};

export default class SignUpForm extends Component {

  static propTypes = {
    onSignUp: PropTypes.func.isRequired
  }
  
  state = {
    login: '',
    email: '',
    password: '',
    agreed: false,
    gender: null,
    age: '',
    errors: null,
  };

  loginInputId = shortid.generate();
  emailInputId = shortid.generate();
  passwordlInputId = shortid.generate();
  checkedInputId = shortid.generate();
  radioMaleInputId = shortid.generate();
  radioFemaleInputId = shortid.generate();
  selectInputId = shortid.generate();

  // handleFocus = (e) => {
  //   e.target.placeholder = '';
  // }

  handleChange = e => {
    this.setState({
      [e.target.name]:
        e.target.type === 'checkbox' ? e.target.checked : e.target.value,
    });
  };

  handleSubmit = e => {
    e.preventDefault();

    const { login, email, password } = this.state;

    validateAll({ login, email, password }, rules, messages)
      .then(data => {
        this.props.onSignUp({ ...this.state });
     
        this.reset();
      })
      .catch(errors => {
        const formatedErrors = {};

        errors.forEach(error => (formatedErrors[error.field] = error.message));

        this.setState({
          errors: formatedErrors,
        });
      });
  };

  reset = () => {
    this.setState({
      login: '',
      email: '',
      password: '',
      agreed: false,
      gender: null,
      age: '',
      errors: null
    });
  };

  render() {
    const { login, email, password, agreed, gender, age, errors } = this.state;

    return (
       <Fragment>
      {/* FORM - login, email, password */}
      <form className={styles.form} onSubmit={this.handleSubmit}>
        <label htmlFor={this.loginInputId}>
          Login
          <input
            type="text"
            value={login}
            name="login"
            placeholder='Введите логин...'
            onChange={this.handleChange}
            id={this.loginInputId}
          ></input>
          {errors && (
            <span style={{ color: '#ff0000', display: 'block'}}>{errors.login}</span>
          )}
        </label>
        <label htmlFor={this.emailInputId}>
          Email
          <input
            type="email"
            value={email}
            name="email"
            placeholder='Введите почту...'
            onChange={this.handleChange}
            id={this.emailInputId}
          ></input>
                    {errors && (
            <span style={{ color: '#ff0000', display: 'block'}}>{errors.email}</span>
          )}
        </label>
        <label htmlFor={this.passwordlInputId}>
          Password
          <input
            type="password"
            value={password}
            name="password"
            placeholder='Введите пароль...'
            onChange={this.handleChange}
            id={this.passwordlInputId}
          ></input>
                    {errors && (
            <span style={{ color: '#ff0000', display: 'block'}}>{errors.password}</span>
          )}
        </label>

        {/* CHECKBOX */}
        <label htmlFor={this.checkedInputId}>
          Agree to terms
          <input
            type="checkbox"
            checked={agreed}
            onChange={this.handleChange}
            name="agreed"
            id={this.checkedInputId}
          />
        </label>

        {/* RADIO BUTTON */}
        <section>
          <h2 className={styles.h2}>Choose your gender:</h2>
          <label htmlFor={this.radioMaleInputId}>
            Male
            <input
              type="radio"
              name="gender"
              checked={gender === Gender.MALE}
              value={Gender.MALE}
              onChange={this.handleChange}
              id={this.radioMaleInputId}
            />
          </label>
          <label htmlFor={this.radioFemaleInputId}>
            Female
            <input
              type="radio"
              name="gender"
              checked={gender === Gender.FEMALE}
              value={Gender.FEMALE}
              onChange={this.handleChange}
              id={this.radioFemaleInputId}
            />
          </label>
        </section>

        {/* SELECT */}
        <label htmlFor={this.selectInputId}>
          Choose your age
          <select
            name="age"
            value={age}
            onChange={this.handleChange}
            id={this.selectInputId}
          >
            <option value="" disabled>
              ...
            </option>
            <option value="18-25">18-25</option>
            <option value="26-35">26-35</option>
            <option value="36+">36+</option>
          </select>
        </label>

        {/* BUTTON SUBMIT */}
        <button type="submit" className={styles.button}>
          Sign up as {login}
        </button>
      </form>
     
      </Fragment>
    );
  }
}
